.contenedor-modal-cerrado {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15vw;
  height: 20vh;
  background-color: var(--moradoNormal);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  box-shadow: 2px 3px 4px black;
  border-radius: 20px;
}
.contenedor-texto-modal-cerrado {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: large;
}
.contenedor-botones-modal-cerrado {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
}

.contenedor-texto-modal-cerrado p {
  color: white;
}

.contenedor-botones-modal-cerrado button {
  padding: 10px;
  color: black;
  background-color: white;
  border: 1px solid white;
  transition: all 0.3s ease;
  cursor: pointer;
  border-radius: 20px;
  min-height: 40px;
  min-width: 40px;
}
.contenedor-botones-modal-cerrado button:hover {
  background-color: var(--amarillo);
}