.card-post2 {
  height: auto;
  width: 383px;
  margin-bottom: 20px;
  margin-top: 4%;
  margin-left: 15%;

  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
  border-radius: 20px;
  background-color: var(--moradoClaro);
}

.contenedor-imagen2 {
  justify-content: center;
  display: flex;
	position: absolute;
	top: 0px;
}
.card-header{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.img2{
  height: 500px;
  max-width: 100%;
  display: block;
  object-fit:cover
}

.text-post2 p {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  margin-left: 20px;
  line-height: 24px;
  display: flex;
  color: #000000;
}

#main-div:hover {
  transform: scale(1.009);
}

#post-imagen {
  left: -20px;
}
