.comments {
  justify-content: space-evenly;
  align-items: center;
  margin-top: 20px;
  margin-left: 25px;
  margin-bottom: 25px;
  background-color: var(--moradoClaro);
  width: 60%;
}

.comment {
  background-color: white;
  border-radius: 20px;
}

.comments-title {font-family: 'Poppins';
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 120.5%;
  margin-top: 90px;
}

.comments-container {
  margin-top: 20px;
  width: 100%;
}

.contenedor-comment-form {
  background-color: var(--moradoNormal);
  border-radius: 20px;
  width: 100%;
  height: 100%;
}

.comment-form-title {
  font-size: 22px;
  width: 100%;
  margin-left: 15px;
}

.comment-form-textarea {
  width: 95%;
  height: 80px;
  margin-bottom: 8px;
  margin-left: 15px;
  margin-top: 6px;
  border-radius: 20px;
  padding: 5px;
  padding-left: 8px;
}

.comment-form-button {
  margin-left: 86%;
  margin-bottom: 5px;
  font-size: 16px;
  width: 10%;
  height: 6vh;
  border-radius: 20px;
  background-color: var(--amarillo);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
  color: black;
}

.comment-form-button:hover {
  border: 1px solid var(--amarillo);
  transform: scale(0.95);
}

.comment-form-button:hover:enabled {
  cursor: pointer;
  background: purple;
}

.comment-form-button:disabled {
  cursor: default;
}

.comment-form-cancel-button {
  margin-left: 10px;
}

.comment {
  display: flex;
  margin-bottom: 28px;
}

.comment-image-container {
  margin-right: 12px;
  margin-left: 5px;
  margin-top: 5px;
}

.comment-image-container img {
  border-radius: 50px;
}

.comment-right-part {
  width: 100%;
}

.comment-content {
  display: inline-block;
}

.comment-author {
  margin-right: 8px;
  font-size: 20px;
  color: purple;
}

.comment-text {
  font-size: 18px;
}

.comment-actions {
  display: flex;
  font-size: 12px;
  color: rgb(51, 51, 51);
  cursor: pointer;
  margin-top: 8px;
}

.comment-action {
  margin-right: 8px;
}

.comment-action:hover {
  text-decoration: underline;
}

.replies {
  margin-top: 20px;
}
