.card-post {
  cursor: pointer;
  height: 210px;
  width: 485px;
  margin-bottom: 20px;
  margin-top: 7%;
  margin-left: 15%;

  position: relative;
	overflow: hidden;
	display: flex;
  
  text-align: center;
	align-items: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  border-radius: 20px;

  background-color: #ffff;
}

.contenedor-imagen img {
	position: absolute;
	top: 0px;
}

.content-post {
	position: absolute;
  top: -30px;
	right: 19%;
}

.header-post h1 {
	font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
}

.text-post p {
  width: 71px;
  height: 24px;
  left: 431px;
  top: 746px;
  margin-left: 25px;

  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  
  line-height: 24px;
  display: flex;
  align-items: center;
  justify-content: start;
  color: #000000;
}

.container-icons img, .container p {
  flex-grow: 0;
  position: relative;
}

#main-div:hover {
  transform: scale(1.009);
}

#post-imagen {
left: -20px;
}

.buttonShare {
  cursor: pointer;
  padding: 0px 20px;
  right: 0;
  bottom: 0;
  margin-bottom: 9%;
  outline: none;
  position: absolute;
  background-color: #ffff;
  border-style: none;
}

.popup-arrow { 
  cursor: pointer; color: rgb(228, 228, 228);}
[role='tooltip'].popup-content { 
  
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  border-radius: 20px;
  background-color: rgb(234, 234, 234)
}

.qrCode {
  display: none
}
