#lost-page {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: fit-content;
  padding: 80px 0;
  background-color: #cecff6;
}

.contenedor-carousel {
  width: 45%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contenedor-formulario {
  width: 50%;
  height: fit-content;
  min-height: 400px;
  text-align: center;
  padding: 3%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: white;
  border-radius: 20px 0px 20px 20px;
}
.contenedor-formulario h1 {
  font-size: 2rem;
  text-transform: uppercase;
}
.formulario {
  border: 1px solid black;
  padding: 10px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 70%;
}


.boton-mapa-form {
  padding: 7px;
  border-radius: 5px;
  background-color: white;
  color: black;
  cursor: pointer;
  margin-top: 10px;
  border-radius: 34.2575px;
}

.label-input {
  margin-top: 10px;
  align-self: flex-start;
}

.tipo-formulario {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.contenedor-a√±adir-ubicacion {
  display: flex;
  justify-content: center;
  align-items: center;
}
/*--------------------------------------------------------------
# Pagina 1
--------------------------------------------------------------*/
.seccion-form {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  width: 100%;
  padding: 20px 0;
  gap: 10px;
}
.contenedor-btn-primera-pag {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 10px;
}
.contenedor-btn-primera-pag button {
  border: 1px solid black;
  border-radius: 34.2575px;
  width: 97px;
  height: 40px;
  background: none;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
#raza-mascota {
  width: 50%;
  padding: 4px;
  margin: 1px;
}
.boton-siguiente-form {
  transition: all 0.3s ease;
  cursor: pointer;
  background: none;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  min-width: fit-content;
  cursor: pointer;
}
.boton-siguiente-form:hover {
  color: white;
  background-color: var(--moradoOscuro);
}
.boton-mapa-form {
  width: 195px;
  height: 40px;
  border-radius: 5px;
  background-color: white;
  color: black;
  cursor: pointer;
  margin-top: 10px;
  border-radius: 34.2575px;
}
#informacion-adicional {
  resize: none;
  width: 100%;
  margin: 2px;
}
.contenedor-btn-prev-next{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.boton-enviar-form{
  width: 100px;
  height: 100px;
  background: none;
  border-radius: 20px;
  font-size: 20px;
  transition: all 0.3s ease;
  cursor: pointer;
}
.boton-enviar-form:hover{
  background-color: var(--moradoOscuro);
  color: white;
}
.boton-enviar-form:first-child{
  margin-right: 20px;
}
/*--------------------------------------------------------------
# Media Queries
--------------------------------------------------------------*/

@media (max-width: 481px) {
  #titulo {
    font-size: 4rem;
  }
}
@media (min-width: 481px) {
  #titulo {
    font-size: 6rem;
  }
}
@media (max-width: 651px) {
  #lost-page {
    display: block;
  }
  .contenedor-carousel {
    height: 30vh;
    width: 100vw;
  }
  .contenedor-formulario {
    height: fit-content;
    width: 100vw;
  }
}
@media (min-width: 961px) {
  #titulo {
    font-size: 7rem;
  }
}
@media (min-width: 1023px) {
  #titulo {
    font-size: 10rem;
  }
}
