.contenedor-modal-mapa {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70vw;
  height: 70vh;
  background-color: var(--moradoNormal);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  box-shadow: 2px 3px 4px black;
  border-radius: 20px;
}

.contenedor-modal-mapa-info{
  width: 30%;
  display:flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  gap: 32px;
}
.contenedor-buscador-ubicaciones{
  position: relative;
}

.contenedor-buscador-ubicaciones button{
  position: absolute;
  right: 5%;
  top: 20%;
  background-color: transparent;
  border: none;
}

.buscador-ubicaciones {
  background-color: white;
  border-radius: 30px;
  height: 30px;
  border: none;
}

.contenedor-botones-modal-mapa {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.contenedor-botones-modal-mapa button {
  padding: 10px;
  width: 100px;
  background-color: transparent;
  transition: all 0.3s ease;
  cursor: pointer;
  background-color: var(--amarillo);
  color: black;
  border: none;
  border-radius: 20px;
}
.contenedor-botones-modal-mapa button:hover {
  background-color: var(--amarillo);
}

.contendor-info-posicion{
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.contendor-icono-ciudad{
  display: flex;
  flex-wrap: row;
  gap: 5px;
}

.latitud-longitud{
  display: flex;
  flex-direction: column;
  gap:5px
}

.contenedor-mapa {
  width: 70%;
  height: 100%;
  overflow: hidden;
}
.boton-cerrar-modal-mapa {
  position: absolute;
  top: 10px;
  right: 10px;
  transition: all 0.3s ease;
  cursor: pointer;
  padding: 7px;
  z-index: 9999;
  background-color: var(--moradoNormal);
  border: none;
  font-size: 20pt;
}
