.fondo-modal {
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.4);
  z-index: 99;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.contenedor-modal-creado {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15vw;
  height: 20vh;
  background-color: var(--moradoNormal);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  box-shadow: 2px 3px 4px black;
  border-radius: 20px;
}
.contenedor-texto-modal-creado {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: large;
}
.contenedor-botones-modal-mensaje-creado {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
}

.contenedor-texto-modal-creado p {
  color: white;
}

.contenedor-botones-modal-mensaje-creado button {
  padding: 10px;
  color: black;
  background-color: white;
  border: 1px solid white;
  transition: all 0.3s ease;
  cursor: pointer;
  border-radius: 20px;
  min-height: 40px;
  min-width: 40px;
}
.contenedor-botones-modal-mensaje-creado button:hover {
  background-color: var(--amarillo);
}