.contenedor-visualizador-completo {
  display: grid;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--moradoClaro);
  background-image: url("../../recursos/Landing-navbar/ilustracion-info-mascota.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.contenedor-mascota {
  width: 100vw;
  margin-top: 100px;
  position: relative;
  overflow: hidden;
  text-align: center;
}

.contenedor-datos-mascota {
  display: inline-grid;
  width: 70%;
  padding: 10px;
  height: 50%;
  gap: 20px;
  margin-left: 10%;
}

.boton-mapa-post {
  font-size: 16px;
  width: 50%;
  height: 6vh;
  border-radius: 20px;
  background-color: var(--amarillo);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
  color: black;
  margin-top: 20%;
}

.boton-mapa-post:hover {
  border: 1px solid var(--amarillo);
  transform: scale(0.95);
}

.contenedor-datos-mascota div {
  border-radius: 20px;
  padding: 20px;
}

.contenedor-datos-carousel {
  display: flex;
}

.contenedor-button-share{
 
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row: 1;
}

.nombre-sexo-mascota {
  display: flex;
  flex-direction: row;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 1;
}


.buttonShareVisualizador {
  margin-top: 10%;
  margin-left: 85%;
  cursor: pointer;
  padding: 0px 10px;
  position: relative;
  background-color: #ffff;
 
border-radius: 165px;
  border-style: none;

}

.nombre-sexo-mascota h1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
}

.localizacion-mascota {
  
  text-align: left;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 4;
  background-color: var(--moradoOscuro);
  color: white;
}

.contenedor-fecha-desaparicion {
  
  text-align: left;
  margin-top: 10%;
}

.especie-raza-mascota {
  grid-column-start: 2;
  grid-column-end: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--moradoOscuro);
  color: white;
}

.datos-publicador {
  text-align: left;
  grid-column-start: 2;
  grid-column-end: 3;
  background-color: var(--moradoOscuro);
  color: white;
}

.informacion-adicional {
  grid-column-start: 1;
  grid-column-end: 3;
  text-align: left;
  background-color: var(--moradoNormal);
}

.contenedor-botones-compartir {
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: space-evenly;
  width: fit-content;
  height: 100%;
  width: 100%;
  background-color: var(--moradoClaro);
}

.contenedor-botones a {
  font-size: 50px;
  color: purple;
}
.contenedor-botones .boton-encontrado {
  font-size: 17px;
  width: 25%;
  height: 6vh;
  border-radius: 20px;
  background-color: var(--moradoNormal);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
  color: black;
  
}

.contenedor-botones .boton-encontrado:hover {
  background-color: var(--moradoOscuro);
  border: 1px solid black;
  
}

.contenedor-botones {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: fit-content;
  height: 100%;
  width: 100%;
  background-color: var(--moradoClaro);
}

.contenedor-botones a {
  font-size: 50px;
  color: purple;
}
.contenedor-botones button {
  font-size: 16px;
  width: 15%;
  height: 6vh;
  border-radius: 20px;
  background-color: var(--amarillo);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
  color: black;
}

.boton-cerrar{
  margin-left: 9%;
}

.boton-avisar{
  margin-left: 1%;
}

.contenedor-botones button:hover {
  border: 1px solid var(--amarillo);
  transform: scale(0.95);
}

.carousel-visualizador {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 20px;
  overflow: hidden;
  margin-top: 8%;
}

.carousel-visualizador button{
    margin: 10px;
    background-color: transparent;
    border: none;
}

.carousel-inner-visualizador {
  white-space: nowrap;
  height: 400px;
  width: 400px;
  border-radius: 20px;
  background-color: var(--moradoNormal);
  overflow: hidden;
  position: relative;
  margin-bottom: 10px;
}
.carousel-inner-visualizador input {
  height: 100%;
  width: 100%;
  opacity: 0;
  cursor: pointer;
}
.carousel-inner-visualizador p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 10;
  font-size: 20px;
  color: white;
}
.carousel-inner-visualizador img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.carousel-item-visualizador {
  display: inline-block;
  position: relative;
  width: 100%;
  overflow: hidden;
  height: 100%;
}



.contenedor-comentarios{
  margin-left: 10%;
}

