.fondo-modal {
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.4);
  z-index: 99;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.contenedor-modal-texto {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 50vw;
  background-color: var(--moradoNormal);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  border: none;
  box-shadow: 2px 3px 4px black;
  padding: 30px;
  border-radius: 20%;
}
.contenedor-botones-modal-mensaje {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.boton-cerrar-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  border: 1px solid white;
  transition: all 0.3s ease;
  cursor: pointer;
  border-radius: 5px;
  padding: 7px;
}
.boton-cerrar-modal:hover {
  color: white;
  background-color: red;
}
#mensaje-mascota-encontrada {
  width: 80%;
  resize: none;
}
