#contenedor-busqueda{
  width: 100vw;
  height: 20vh;
  min-height: 300px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  gap: 40px;
  background-color: white;
}
.contenedor-botones-busqueda{
  width: 40%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.contenedor-botones-especies{
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--moradoClaro);
  border-radius: 25px 25px 0 0;
}
.contenedor-botones-especies .groupBotones{
  width: 100%;
  height: 100%;
  border-radius: 25px 25px 0 0;
  background-color: var(--moradoClaro);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
  color: var(--moradoNormal);
}

.contenedor-botones-especies button{
  width: 20%;
  height: 100%;
  border-radius: 25px 25px 0 0;
  background-color: var(--moradoClaro);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: none;
  color: var(--moradoNormal);
}


.contenedor-botones-especies button:hover{
  background-color: var(--moradoNormal);
  border: 1px solid var(--moradoOscuro);
  color: var(--moradoOscuro);
}

.contenedor-botones-generales-busqueda{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.contenedor-botones-generales-busqueda .componente-busqueda{
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contenedor-botones-generales-busqueda select{
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}
.componente-busqueda{
  width: 100%;
  background-color: var(--moradoClaro);
  border-radius: 20px;
  padding: 10px;
  color: var(--moradoOscuro);
}
