.contenedor-busqueda-imagenes{
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 40px;
  border: 1px solid var(--moradoOscuro);
  border-radius: 20px;
}
.contenedor-busqueda-imagenes img{
  height: 100%;
}
.texto-busqueda-imagenes p{

}
.texto-busqueda-imagenes h1{
  color: var(--moradoOscuro);
}