@media (max-width: 481px) {
    #titulo-landing {
      font-size: 35px;
      text-align: center;
      width: 100%;
    }
    .wrapper-info-landing{
      padding: 0;
    }
    .contenedor-botones-landing {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
    }
    .contenedor-botones-landing a {
      width: 300px;
    }
    .contenedor-botones-landing a:first-child {
      margin-right: 0;
      margin-bottom: 30px;
    }

    /*--------------------------------------------------------------
    # About
    --------------------------------------------------------------*/
    .titulo-seccion-landing{
        width: 100%;
        text-align: center;
        padding: 30px 0;
    }
    #seccion-about-landing{
        height: auto;
    }
    .contenedor-texto-about{
        flex-direction: column;
        padding: 10px;
        gap: 20px;
        text-align: center;
    }
    .texto-about-izquierda{
        width: 90%;
        height: auto;
        border-radius: 20px;
        padding: 20px;
    }
    .texto-about-derecha{
        width: 90%;
        height: auto;
        border-radius: 20px;
        padding: 20px;
    }
    .texto-about-izquierda p, .texto-about-derecha p{
        padding-top: 20px;
        width: 100%;
        font-size: 15px;
      }
      .texto-about-izquierda h3, .texto-about-derecha h3{
        width: 100%;
      }
/*--------------------------------------------------------------
# Consejos
--------------------------------------------------------------*/
    .contenedor-texto-consejos{
        flex-direction: column;
    }
    #seccion-consejos-landing{
        height: auto;
        padding-bottom: 5vh;
    }
/*--------------------------------------------------------------
# Actualizaciones
--------------------------------------------------------------*/
#seccion-actualizaciones-landing{
    height: auto;
}
.contenedor-texto-actualizaciones{
    flex-direction: column;
}
.texto-actualizaciones{
    width: 90%;
}
.imagen-actualizaciones{
    width: 90%;
}

/*--------------------------------------------------------------
# FAQ
--------------------------------------------------------------*/
#seccion-faq-landing{
    height: auto;
}
.contenedor-pregunta-faq{
    align-self: center !important;
    width: 90%;
    text-align: center;
}
.pregunta-boton-faq{
    flex-direction: column;
}
.texto-pregunta-FAQ{
    background-color: white;
}
  }
