.contenedor-construccion {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f7f7f7;
  height: 100vh;
  padding: 10%;
}

#imagen-constuyendo {
  width: 40%;
}

.text-container h1 {
  font-size: 3rem;
  line-height: 3rem;
}

.text-container p {
  padding: 2.5vh 0;
  font-size: large;
}
.text-container a {
  white-space: nowrap;
  border: 1px solid black;
  padding: 10px;
  border-radius: 30px 30px;
  margin-right: 4px;
  transition: all 0.4s ease-in-out;
}

.text-container a:hover {
  background-color: purple;
  color: white;
  border-radius: 30px 50px;
  border-color: transparent;
  content: "Holaa";
}

/*--------------------------------------------------------------
# Media Queries
--------------------------------------------------------------*/

@media (max-width: 961px) {
  .contenedor-construccion {
    flex-direction: column;
  }
  .text-container {
    text-align: center;
    padding-bottom: 7vh;
  }
  #imagen-constuyendo {
    width: 80%;
  }
}
