.contenedor-modal-mapa-visualizador {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 70%;
  background-color: var(--moradoNormal);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  box-shadow: 2px 3px 4px black;
  border-radius: 20px;
  z-index: 1000;
}

.contendor-info-posicion {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.contendor-icono-ciudad {
  display: flex;
  flex-wrap: row;
  gap: 5px;
}

.contenedor-mapa-visualizador {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.leaflet-touch .leaflet-control-layers,
.leaflet-touch .leaflet-bar {
  border: none;
  color: var(--amarillo);
  border-radius: 20%;
}

.leaflet-touch .leaflet-bar a:first-child {
  border-radius: 20px;
}

.leaflet-touch .leaflet-bar a:last-child {
  border-radius: 20px;
}

.boton-cerrar-modal-mapa-visualizador {
  position: absolute;
  top: 10px;
  right: 10px;
  transition: all 0.3s ease;
  cursor: pointer;
  padding: 7px;
  z-index: 9999;
  background-color: var(--moradoNormal);
  border: none;
  font-size: 20pt;
}
