
/*--------------------------------------------------------------
# Landing page
--------------------------------------------------------------*/

#landing-page {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  background-image: url("../../recursos/Landing-navbar/ilustracion-home.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.wrapper-info-landing{
  display: flex;
  align-items: start;
  justify-content: center;
  width: 100vw;
  height: 100%;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.2);
  padding-left: 10vw;
}

#titulo-landing {
  font-size: 85px;
  white-space: nowrap;
  text-transform: uppercase;
}
.contenedor-botones-landing {
  padding-top: 8vh;
}

.contenedor-botones-landing a {
  text-decoration: none;
  color: black;
  background-color: var(--amarillo);
  padding: 10px 25px;
  font-size: 20px;
  border-radius: 40px;
  transition: all 0.3s ease-in-out;
  text-align: center;
  font-weight: 400;
  display: inline-block;
}
.contenedor-botones-landing a:hover {
  border: 1px solid var(--amarillo);
  transform: scale(0.95);
}

.contenedor-botones-landing a:first-child {
  margin-right: 10px;
}


/*--------------------------------------------------------------
# About
--------------------------------------------------------------*/
#seccion-about-landing{
  height: 70vh;
  width: 100vw;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  flex-direction: column;
  min-height: 600px;
}
.titulo-seccion-landing{
  font-size: 40px;
}
.contenedor-texto-about{
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color:#FFF3D4;
}

.texto-about-izquierda{
  width: 50%;
  height: 100%;
  padding-left: 50px;
  padding-top: 8vh;
  border-top-right-radius: 600px;
  background-color: var(--moradoClaro);
}
.texto-about-izquierda p, .texto-about-derecha p{
  padding-top: 50px;
  width: 70%;
  font-size: 15px;
}
.texto-about-izquierda h3, .texto-about-derecha h3{
  width: 70%;
  font-size: 32px;
  color: var(--moradoOscuro);
}
.texto-about-izquierda h3 svg,.texto-about-derecha h3 svg{
  padding-right: 10px;
}
.texto-about-derecha{
  width: 50%;
  height: 100%;
  padding-right: 50px;
  padding-top: 8vh;
  padding-left: 10vw;
  border-bottom-left-radius: 600px;
  background-color: var(--moradoClaro);
  overflow: hidden;
}
.titulo-seccion-landing{
  padding-left: 5vw;
}
/*--------------------------------------------------------------
# Consejos
--------------------------------------------------------------*/
#seccion-consejos-landing{
  height: 60vh;
  width: 100vw;
  min-height: 600px;
}
.contenedor-texto-consejos{
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 5%;
  padding: 0 5vw;
}
.componente-consejos{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 70%;
  text-align: center;
}
.componente-consejos h3{
  color: var(--moradoNormal);
  padding: 30px 0;
}
.componente-consejos img{
  height: 150px;
  width: auto;
}
/*--------------------------------------------------------------
# Actualizaciones
--------------------------------------------------------------*/
#seccion-actualizaciones-landing{
  height: 60vh;
  width: 100vw;
  min-height: 600px;
  background-color: var(--moradoClaro);
  padding: 30px;
  background-image: url("../../recursos/Landing-navbar/ilustracion-info-mascota.png");
  background-size: auto;
  background-position: center;
  
}
.contenedor-texto-actualizaciones{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 10%;
  gap: 5%;
  height: 80%;
  font-size: 15px;
}
.texto-actualizaciones{
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.contenedor-imagen-actualizaciones{
  width: 60%;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  height: 100%;
  position: relative;
}
.texto-fb-landing{
  background-color:white ;
  border: 1px solid black;
  height: 60%;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 20px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.texto-fb-landing h3{
  color: var(--moradoOscuro);
  font-size: 32px;
}
.texto-fb-landing p{
  font-size: 16px;
}
.contenedor-imagenes-fb{
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translateY(-61%);
}
/*--------------------------------------------------------------
# FAQ
--------------------------------------------------------------*/
#seccion-faq-landing{
  width: 100vw;
  min-height: 600px;
  padding: 30px;
}
.contenedor-preguntas{
  padding: 10vh 2vw 10vh 2vw;
  display: flex;
  align-items: center;
  width: 100%;
  flex-direction: column;
}
.contenedor-pregunta-faq{
  width: 40%;
  height: 200px;
  min-height: max-content;
}
.pregunta-boton-faq{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  background-color: var(--moradoNormal);
  padding: 20px;
  border-radius:20px 20px 0 0;
  z-index: 10;
}

.texto-pregunta-FAQ{
  padding: 20px;
  border-radius: 0 0 20px 20px;
  transition:transform 0.5s ease;
  transform-origin: top;
}

.pregunta-boton-faq button{
  background: none;
  border: none;
  cursor: pointer;
}
.texto-pregunta-FAQ-hide{
  transform: scaleY(0);
}
.texto-pregunta-FAQ-show{
  transform: scaleY(1);
  border: 1px solid black;
}

.contenedor-pregunta-faq:nth-child(odd){
  align-self: flex-end;
}
.contenedor-pregunta-faq:nth-child(even){
  align-self: flex-start;
}


