#footer {
  width: 100%;
  height: 200px;
  background-color: #131313;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 2px solid black;
  color: white;
}
.contenedor-links-footer img{
  padding-bottom: 20px;
}
#contenedor-footer {
  display: block;
}
.links-social-media-footer img{
  padding: 10px;
}
.contenedor-copy {
  text-align: center;
}
@media (max-width: 481px) {
  #footer{
    flex-direction: column;
    height: auto;
    padding: 20px;
  }
  .contenedor-links-footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-bottom: 30px;
    text-align: center;
  }
}