.contenedor-posts2 {
  overflow-x: scroll;
  overflow-y: hidden;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.circles{
  margin-top: 150px;
  margin-left: 6%;
  overflow-x: scroll;
  overflow-y: hidden;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

