:root{
  --rosa: #FFF3D4;
  --moradoClaro:#CECFF6;
  --moradoNormal: #8184D0;
  --moradoOscuro: #4F4688;
  --amarillo:#FDDC31; 
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Poppins',Arial, Helvetica, sans-serif;
}

html,
body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: black;
}

.redondeado{
  border-radius: 20px;
}
